.k-combobox .k-dropdown-wrap {
  border-color: rgba(0, 0, 0, 0.08);
  color: #656565;
  background-color: #ffffff;
  border: 0;
  border-bottom: 0px solid #dcdcdc;
}

.k-dropdown {
  width: 69%;
  border-width: 0;
  box-sizing: border-box;
  outline: 0;
  background: none;
  font-family: inherit;
  font-size: 14px;
  line-height: 1.4285714286;
  text-align: left;
  white-space: nowrap;
  display: inline-flex;
  vertical-align: middle;
  position: relative;
  -webkit-touch-callout: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.k-dropdown .k-dropdown-wrap,
.k-dropdowntree .k-dropdown-wrap {
  border-color: #ffffff;
  color: #656565;
  background-color: #ffffff;
  background-image: none;
}

.k-checkbox.k-invalid {
  border: solid 1px #e2e2e2;
}

.k-checkbox:focus {
  border: solid 1px #e2e2e2;
  box-shadow: none;
}

.k-checkbox:checked:focus {
  border: none;
  box-shadow: none;
}

.k-checkbox:checked {
  border: solid 1px #e2e2e2;
  color: #ffffff;
  background-color: #000f34;
}

.k-maskedtextbox {
  /* transform: translate(20px, -4px);
  width: 80% !important; */
}

#hpNo1.k-textbox {
  /* width: 18% !important; */
  padding-right: 0px;
  text-align: left;
}

#insertInfoPage .k-maskedtextbox {
  /* width: 50% !important; */
}

.k-maskedtextbox .k-textbox {
  padding-left: 3px;
  border-color: rgba(0, 0, 0, 0.08);
  color: #656565;
  background-color: #ffffff;
  border: 0;
  border-bottom: 0px solid #dcdcdc;
}
.k-textbox::placeholder {
  font-family: NanumGothic;
  font-size: 20px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  letter-spacing: normal;
  padding: 13px 15.3px 12px 0px;
  color: #e2e2e2 !important;
  margin-bottom: 10px;
}

#hpNo2.k-textbox::placeholder {
  font-family: NanumGothic;
  font-size: 15px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  letter-spacing: normal;
  padding: 13px 15.3px 12px 0px;
  color: #e2e2e2 !important;
  margin-bottom: 10px;
}

#hpModelOption .k-dialog-actions {
  display: none;
}

#hpModelOption.k-header {
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;
  height: 50px;
}

#hpModelOption .k-dialog-title {
  font-family: NanumGothic;
  font-size: 15px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.13;
  letter-spacing: normal;
  text-align: left;
  color: #ffffff;
}

div[aria-labelledby='hpModelOption'].k-window {
  background-color: transparent;
}

div[aria-labelledby='hpModelOption'] .k-window-content {
  padding: 56px 16px;
  background-color: #f8f8f8;
  height: 218px;
  flex: none;
}

div[aria-labelledby='hpModelOption'] .k-dialog-buttongroup {
  border-width: 0 0 0;
  background-color: #ffffff;
}

div[aria-labelledby='hpModelOption'] .k-dropdown .k-dropdown-wrap {
  border-color: #ffffff;
  color: #656565;
  background-color: #ffffff;
  background-image: none;
  border-radius: 0px;
  height: 40px;
  border-bottom: solid 1px #e2e2e2;
}

div[aria-labelledby='hpModelOption'] .k-dropdown {
  width: 100%;
}

div[aria-labelledby='hpModelOption'] .k-dropdown .k-input {
  align-self: center;
  justify-content: flex-start;
}
.k-animation-container {
  z-index: 10003;
}

.k-dialog-wrapper .k-dialog[aria-labelledby='bonusModal'] {
  border-radius: 5px;
}
#planInput.k-textbox {
  background-color: transparent;
}
.k-i-question::before {
  font-size: 20px;
}
