@font-face {
  font-family: 'S-Core';
  src: url('../fonts/SCDream4.otf') format('otf');
}

@font-face {
  font-family: 'NanumSquareBold';
  src: url('../fonts/NanumSquareB.otf') format('otf');
}

@font-face {
  font-family: 'NanumSquareRegular';
  font-weight: normal;
  src: url('../fonts/NanumSquareR.otf') format('otf');
}

@font-face {
  font-family: 'NanumSquareL';
  src: url('../fonts/NanumSquareL.otf') format('otf');
}

@font-face {
  font-family: 'NanumGothic';
  src: url('../fonts/NanumGothic.ttf') format('ttf');
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
}

