/***************************************************************************
* 버거 메뉴 
***************************************************************************/
#bugger-menu {
  text-align: center;
  overflow: auto;
  border-bottom: 1px solid #f1f1f1;
}

/* 햄버거 버튼 싸이즈 */
.bm-burger-button {
  position: fixed;
  width: 26px;
  height: 20px;
  right: 18px;
  top: 17px;
}

.after-login {
  position: fixed;
  width: 28px;
  height: 27px;
  right: 22px;
  top: 20px;
  transition: all 0.5s;
}

.after-login-scroll {
  position: fixed;
  width: 28px;
  height: 27px;
  right: 22px;

  transform: translateY(0px);
  transition: all 0.5s;
}

/* 햄버거 버튼 색상 */
.bm-burger-bars {
  background: #f07474;
}

/* 닫기 버튼 싸이즈 */
.bm-cross-button {
  height: 24px;
  width: 24px;
  left: 8px;
  z-index: 99;
}

/* 닫기 버튼 색상 */
.bm-cross {
  background: #bdc3c7;
}

/* 버거 메뉴 스타일 */
.bm-menu {
  background: #fff;
  padding: 2.5em 1.5em 0;
  font-size: 13px;
}

.bm-menu-wrap {
  top: 0;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
}

/* 메뉴 아이템 */
.bm-item {
  display: inline-block;
  color: #666666;
  text-decoration: none;
  margin-bottom: 10px;
  transition: color 0.2s;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}

.wrap {
  display: flex;
  justify-content: center;
  flex-direction: row;
  width: 100%;
  margin-bottom: 20px;
}

.span {
  width: 50%;
}

.anchor {
  width: 100%;
  color: #f51e1e;
  font-size: 13px;
  border: 1px solid #f51e1e;
  padding: 5px;
  margin: 1px;
}
